.dialogs{
    display: grid;
        grid-template-areas:
        "event event"
        "dialogs messages";
    grid-template-rows: 60px 1fr;
    
    border-radius: 3px;
    width: 100%;
    background-color: #313131;
    margin-bottom: 30px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.07);
}

.dialogsItem{
    padding: 10px;
    grid-area: dialogs;

}



